import React, { useRef, useEffect } from "react";
import { InputText } from "primereact/inputtext";
import { Dropdown } from "primereact/dropdown";
import { TabView, TabPanel } from "primereact/tabview";
import { useSearch } from "../../hooks/useSearch";
import "./Search.scss";
import BrandInfo from "./components/BrandInfo";
import CostInfo from "./components/CostInfo";
import SalesInfo from "./components/SalesInfo";

function Search() {
  const {
    searchType,
    searchWord,
    suggestions,
    companyNo,
    showSuggestions,
    selectedTab,
    setSelectedTab,
    setSearchType,
    setSearchWord,
    highlightIndex,
    suggestionRefs,
    handleInputChange,
    handleSuggestionClick,
    handleKeyDown,
    handleTabChange, //     
    basicInfo,
    setBasicInfo,
    salesInfoRegion,
    salesInfo,
    setSalesInfo,
    setSaleInfoRegion,
    handleSalesInfoRegionChange,
    getCompanyRegionTotalCount,
    costInfo,
  } = useSearch({});

  const inputRef = useRef(null);

  useEffect(() => {
    if (inputRef.current) {
      inputRef.current.focus();
    }
  }, []);

  const searchOptions = [
    { label: "", value: "brand" },
    // { label: "", value: "company" },
  ];

  return (
    <div className="search-page">
      <div className="search-bar">
        <Dropdown
          value={searchType}
          options={searchOptions}
          onChange={(e) => setSearchType(e.value)}
          placeholder="  "
          className="search-dropdown"
        />

        <div className="input-container">
          <InputText
            ref={inputRef}
            value={searchWord}
            onChange={(e) => handleInputChange(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder=" ."
            className="search-input"
          />

          {showSuggestions && suggestions.length > 0 && (
            <ul className="suggestions-list">
              {suggestions.map((suggestion, index) => (
                <li
                  key={suggestion.fir_mst_sn}
                  ref={(el) => (suggestionRefs.current[index] = el)}
                  onClick={() => handleSuggestionClick(suggestion)}
                  className={highlightIndex === index ? "highlight" : ""}
                >
                  {suggestion.name}
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>
      <div className="search-detail">
        <TabView
          activeIndex={
            selectedTab === "sales"
              ? 0
              : selectedTab === "cost"
              ? 1
              : selectedTab === "brandInfo"
              ? 2
              : ""
          }
          onTabChange={(e) => {
            const tabs = ["sales", "cost", "brandInfo"];
            handleTabChange(tabs[e.index]);
          }}
        >
          <TabPanel header=" " disabled={!companyNo}>
            <SalesInfo
              salesInfoRegion={salesInfoRegion}
              handleSalesInfoRegionChange={handleSalesInfoRegionChange} //    
              salesInfo={salesInfo ? salesInfo.data : {}}
            />
          </TabPanel>
          <TabPanel header=" " disabled={!companyNo}>
            <CostInfo costInfo={costInfo ? costInfo.data : []} />
          </TabPanel>
          <TabPanel header=" " disabled={!companyNo}>
            <BrandInfo basicInfo={basicInfo ? basicInfo.data : {}} />
          </TabPanel>
        </TabView>
      </div>
    </div>
  );
}

export default Search;
